@import '../../Variables.scss';

.game-widget {    
    position: relative;
    display: inline-block;
    max-width: 345px;
    width: 100%;
    height: 500px;
    background-color: $bg-light;
    display: inline-block;
    border-radius: 20px;
    box-shadow: 0 6px 8px #0000004d;
    display: inline-block;
    vertical-align: top;
    margin-right: 50px;
    margin-bottom: 50px;
    text-align: left;
    &:nth-child(3n) {
        margin-right: 0;
    }
    @media (max-width: $lg-grid) {
        &:nth-child(3n) {
            margin-right: 50px;
        }
        &:nth-child(2n) {
            margin-right: 0;
        }
    }
    @media (max-width: $md-grid) {
        &:nth-child(2n),
        &:nth-child(3n) {
            margin: 0 auto;
            margin-bottom: 50px;
        }
        display: block;
        margin: 0 auto;
        margin-bottom: 50px;
    }
    .image {
        height: 200px;
        position: relative;
        overflow: hidden;
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
        text-align: left;
        &:before {
            content: '';
            position: absolute;
            height: 100%;
            width: 100%;
            top: 0;
            left: 0;
            display: block;
            width: 100%;
            background-color: transparent;
            box-shadow: inset 0 0 150px 20px #000;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        .text {
            color: $text-lightest;
            position: absolute;
            bottom: 0;
            padding: 20px;
            a {
                text-decoration: none !important;
            }
            h1 {
                color: $text-lightest;
                margin-bottom: 10px;
                line-height: 1.5;
            }
            ul {
                li {
                    display: inline-block;
                    margin-right: 10px;
                    svg {
                        height: 20px;
                        fill: #fff;
                    }
                }
            }
        }
    }
    .details {
        max-height: 300px;
        height: 100%;
        position: relative;
        padding: 20px;
        bottom: 0;
        box-shadow: inset 0 0px 20px #00000030;
        box-sizing: border-box;
        color: $text-lightest;
        border-bottom-left-radius: 20px;
        border-bottom-right-radius: 20px;
        .deck {
            line-height: 1.3;
            font-size: 13px;
            font-weight: unset;
            font-style: italic;
            font-family: 'Montserrat-Light', sans-serif !important;
        }
    }
    footer {
        position: absolute;
        bottom: 0;
        width: 100%;
        padding: 20px;
        box-sizing: border-box;
        margin: 0 auto;
    }
}